<template>
    <div class="product container">
        <div class="row">
            <div class="col-md-12 pt-5">
                <div class="col-md-6 justify-content-center card">
                    <section>
                        <div class="col p-5">
                            <div class="col-12 col-md-12 pt-3">
                                <input class="col-md-12" v-model="loginDetails.username" placeholder="Username">
                            </div>
                            <div class="col-12 col-md-12 pt-3">
                                <input class="col-md-10" :type="type" 
                                v-model="loginDetails.password" placeholder="Password" />
                                <h6  class="text-right pr-1" type="password" @click="switchVisibility">
                                    {{buttontext}}
                                </h6>
                            </div>
                        </div>
                    </section>
                 <footer class="pb-5 text-center">
                     <button type="button" class="btn btn-primary" @click="Login()">LOGIN</button> 
                </footer>
            </div> 
            </div> 
        </div>
       
    </div>
</template>


<script>
// components
    import Vue from 'vue'
    import VueToast from 'vue-toast-notification';
    // Import one of available themes
    import 'vue-toast-notification/dist/theme-default.css';
    //import 'vue-toast-notification/dist/theme-sugar.css';
     Vue.use(VueToast)

export default Vue.extend({
    name: 'Login',

    components: {
    
    },
    props: {
       
    },
    data() {
      return {
        toast:Vue.$toast,
        loginDetails:{},
        type:'password',
        buttontext:'SHOW',
      }
    },
    methods: {
      switchVisibility(){
        //   this.type = this.type === 'password' ? 'text' : 'password'
          if(this.type === 'password'){
              this.type = 'text'
              this.buttontext = 'HIDE'
          }
          else{
              this.type = 'password'
              this.buttontext = 'SHOW'
          }
      },
      Login() {
        console.log("Login Details for user",this.loginDetails)
        if(this.loginDetails.username == 'admin' && this.loginDetails.password=="1234567890"){
            this.toast.success("Logged in",{
                        position:'bottom-right'
            })
            localStorage.setItem('token', this.loginDetails);
            localStorage.setItem('isLoginUser', this.loginDetails.username);
            // window.location.href = "/admin/productlist"
            this.$router.push({path:'/admin/productlist'})
        }
        else {
            this.toast.error("Login credentials are incorrect",{
                        position:'bottom-right'
            })
        }
      }
    },
  
    created() {
          
    }
  })
</script>
<style  scoped>
template{
  padding:2%;
}
</style>