<template>

    <div class="product-card">

        <div  v-if="isLoginUser == null">
            <router-link to="/admin/login" >Login</router-link></div>
        <div @click="logout()" v-if="isLoginUser=='admin'">
            <router-link to="/" >Logout</router-link></div>
        <!-- <router-link to="/admin/productlist"  v-if="isLoginUser=='admin'">admin product list</router-link>
        <br/>
        <router-link to="/admin/product/1"  v-if="isLoginUser=='admin'">Create product</router-link> -->
        <!-- <router-link to="/admin/productlist" >admin product list</router-link>
        <br/>
        <router-link to="/admin/product/1" >Create product</router-link> -->
        <div class="font-weight-bold" v-on:click="$emit('callProductList', 0,0)">ALL</div>
        <ul class="list-group" v-for="item in categoryList" :key="item.main_category_id">
            <li class="list-group-item text-uppercase  p-0" > 
                <span class="font-weight-bold" href="javascript:void(0)" v-on:click="$emit('callProductList', item.main_category_id, 0)" >
                    <a href="javascript:void(0)">{{item.main_category_name}}</a>
                </span>  
                <ul class="list-group" v-for="subitem in item.sub_category" :key="subitem.subcategory_id"> 
                    <li class="list-group-item subCategory-text text-uppercase py-1" 
                        v-on:click="$emit('callProductList', item.main_category_id, subitem.subcategory_id)">
                       <a href="javascript:void(0)"> {{subitem.subcategory_name}}</a>
                    </li>
                </ul>
            </li>
        </ul>
    </div>
    
</template>
<script>

import Vue from 'vue'
import ProductService from '../../shared/services/product.service'
// import Homecard from '../../pages/site/Home'

    const productService = new ProductService();

    export default Vue.extend ({  
        // components: {
        //     Homecard,
        // },
        data() {
            return {
                categoryList :{},
                subcategoriesList:{},
                isLoginUser:String,
            }
          
        },
        created() {
            this.isLogin();
            productService.getCategoriesList()
            .then(response => {
                this.categoryList = response.data.output.success[0].data
                this.categoryList.forEach((element, index) => {
                    var tempObj = JSON.parse(this.categoryList[index].sub_category);
                    this.subcategoriesList[element['sub_category']]=[];
                        for(var i in tempObj){
                            var obj = {};
                            obj['subcategory_id'] = i;
                            obj['subcategory_name'] = tempObj[i];
                            this.subcategoriesList[element['sub_category']].push(obj);
                        }
                    this.categoryList[index]['sub_category']=this.subcategoriesList[element['sub_category']]
                }); 
            })
            .catch(e => {
            this.errors.push(e)
            })
        },
        
        methods:{
            getLogin(){
                // window.location.href="/admin/login"
                this.$router.push({path:'/admin/login'})
            },
            isLogin(){
                this.isLoginUser =  localStorage.getItem('isLoginUser');
                console.log('login user fjdkfjdafjlj',this.isLoginUser)
            },
            logout(){
                this.clear_localStorage();
                // window.location.href = "/admin/login"
                // window.location.href="/"
                this.$router.push({path:'/'})
            },
            clear_localStorage(){
                localStorage.removeItem('token');
                localStorage.removeItem('isLoginUser');
                localStorage.removeItem('username');
                console.log("localstoreg logout ",localStorage.getItem('isLoginUser'))
            }
        }

   
})
</script>

<style scoped>
.list-group-item{
    border: 0px solid grey!important;
} 
.dropdown-toggle::after {
    display: none !important;
    margin-left: .255em;
    vertical-align: .255em;
    content: "";
    border-top: .3em solid;
    border-right: .3em solid transparent;
    border-bottom: 0;
    border-left: .3em solid transparent;
}
.subCategory-text{
    color: #858585 !important;
}
.dropdown-menu{
    border:0px !important;
    margin:-0.875rem 0 0 !important;
}
.logo{
    padding-bottom: 5rem !important;
    padding-top: 7em !important;
}
.font-weight-bold{
    font-weight:600!important;
    color:#858585;
}
a{
    color:#858585
}
a:hover{
    color:#858585
}
</style>