          </div>
          <div class="row border-top border-bottom border-dark font-weight-bold ">
                <div class="offset-md-3 p-2" >
                    Create Product
                </div> 
          </div>
        <div class="row">
            <div class="col-md-3">
                <app-side-nav class="d-none d-md-block pt-3" v-on:callProductList = "getProductList"></app-side-nav>
                <app-sm-side-nav class="d-block d-md-none pt-3" v-on:callProductList = "getProductList"></app-sm-side-nav>
            </div>
            <div class="col-md-9 pt-5">
                <!-- <HeaderOption  -->
                    <!-- <header-options 
                      v-bind:in_data="headerOptions">
                    </header-options> -->

                <!-- <section>
                    <div class="row pt-10">
                        <div class="col-md-6 col-6">
                            <button type="button" class="btn btn-default border ml-2">
                                <span class="glyphicon glyphicon-plus" > 
                                     <router-link to="/admin/product/1" >Add Product + </router-link></span>
                            </button>
                        </div>
                        <div class="col-md-6 col-6">
                            <button type="button" class="btn btn-default border ml-2">
                                <span class="glyphicon glyphicon-plus" >
                                     <router-link to="/admin/add-category" >Add Category</router-link>
                                </span>
                            </button>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-6 col-6 pt-1">
                            <button type="button" class="btn btn-default border ml-2">
                                <span class="glyphicon glyphicon-plus" >
                                     <router-link to="/admin/add-subcategory" >Add Sub Category</router-link>
                                </span>
                            </button>
                        </div>
                        <div class="col-md-6 col-6 pt-1">
                            <button type="button" class="btn btn-default border ml-2 ">
