                                     <router-link to="/admin/add-brands" >Add Brand</router-link>
                                </span>
                            </button>
                        </div>
                    </div>
                </section> -->
                <section>
                    <!-- <div class="col-md-12"> -->
                    <div class="template-body tab-bg pt-5">
                        <div class="table-responsive pt-5">
                            <table  class="table border pt-5">
                            <tbody  >
                                <tr>
                                <th>S NO</th>
                                <th>Brand Name</th>
                                <th>Product Name</th>
                                <th>Edit</th>
                                <th>Status</th>
                                </tr>
                            </tbody>
                            
                            <tbody>
                                <tr  v-for="(product,index) in productList" :key="index">
                                <td >{{index+1}} </td>
                                <td>{{product.brand_desc}}</td>
                                <td>{{product.product_name}}</td>
                                <td>
                                    <button type="button" class="btn btn-default text-right">
                                        <span class="glyphicon glyphicon-plus" @click="editProduct(product.product_id)">Edit </span>
                                    </button>
                                </td>
                                <td>
                                    <button type="button" class="btn btn-default text-right">
                                        <span class="status-title glyphicon glyphicon-plus"  data-toggle="modal" data-target="#isActivateProduct" @click="getProductid(product.product_id,product.active_status)"> 
                                            {{product.active_status == 1 ? 'ACTIVE' : 'INACTIVE'}} </span> 
                                    </button>
                                    <div class="modal fade" id="isActivateProduct" role="dialog">
                                        <div class="modal-dialog modal-md">
                                            <div class="modal-content">
                                                <div class="modal-header">
                                                    <button type="button" class="close" data-dismiss="modal">&times;</button>
                                                    <h4 class="modal-title"> {{product.active_status == 1 ? 'INACTIVE' : 'ACTIVE'}} the Product</h4>
                                                </div>
                                                <div class="modal-body">
                                                    <p class="body-text"> Would you like to {{product.active_status == 1 ? 'INACTIVE' : 'ACTIVE'}} the Product?</p>
                                                </div>
                                                <div class="modal-footer">
                                                    <button type="button" class="btn btn-default"  data-dismiss="modal">No</button>
                                                    <button type="button" class="btn btn-primary" @click="activeProduct()" data-dismiss="modal">YES</button>
