<script lang="ts">
    import Vue from 'vue'
    import VueToast from 'vue-toast-notification';
    // Import one of available themes
    import 'vue-toast-notification/dist/theme-default.css';
    //import 'vue-toast-notification/dist/theme-sugar.css';
    import ProductService from '../../shared/services/product.service'

    import sideNav from '../../components/layout/side-nav.vue'
    import smSideNav from '../../components/layout/sm-side-nav.vue';
    import HeaderOption from '@/components/common/Header-options'

    Vue.use(VueToast)   
    const productService = new ProductService();


    export default Vue.extend ({ 
        
    components : {
        'app-side-nav':sideNav,
        'app-sm-side-nav':smSideNav,
        'header-options':HeaderOption
    },
         data() {
            return {
                toast:Vue.$toast,
                productList:{},
                activeProductForm:{},
                sc_id:0,
                mc_id:0,
                activeStatusText:String,
                activeProductId:Number,
                activeProductStatus:String,
                headerOptions: {
                    createProduct:'true',
                    createCategory:'true',
                    createSubcategory:'true',
                    createBrand:'true'
                }
            }
        },
        methods: {
            getProductList(mc_id,sc_id) {
                productService.getProductAdminList(mc_id,sc_id)
                .then(response => {
                this.productList = response.data.output.success[0].data.list;
                })
                .catch(e => {
                this.errors.push(e)
                })  
            },

            getProductid(productId,activeFlag){
                this.activeProductId = productId,
                this.activeProductStatus = activeFlag
            },

            activeProduct(){
                if(this.activeProductStatus == 1){
                    this.activeProductStatus = 0;
                    this.activeStatusText = " Inactive"
                }
                else if(this.activeProductStatus == 0){
                    this.activeProductStatus =1;
                    this.activeStatusText = "Active"
                }
                this.activeProductForm['product_id']=this.activeProductId
                this.activeProductForm['active_status'] = this.activeProductStatus
                let _activeProductForm = new FormData()
                   Object.keys(this.activeProductForm).forEach((key) => {
                       _activeProductForm.append(key, this.activeProductForm[key])
                   })
                productService.activeProduct(_activeProductForm)
                .then(response => {
                this.productList = response.data.output.success[0].data;
                   this.getProductList(0,0);
                   this.toast.success("The product is now " +this.activeStatusText,{
                        position:'bottom-right'
                    })
                })
                .catch(e => {
                    this.toast.error("Product Id or Status is Invalid",{
                        position:'bottom-right'
                    })  
                    this.errors.push(e)
                }) 
            },

            editProduct(id){
                let prodid = id;
                // window.location.href = "/admin/product/edit/"+prodid
                this.$router.push({path:'/admin/product/edit/'+prodid})
            }
        },
         created() {
            productService.getProductAdminList(this.mc_id,this.sc_id)
            .then(response => {
            this.productList = response.data.output.success[0].data.list;
            console.log("this.productDEtails product details",this.productList)
            })
            .catch(e => {
            this.errors.push(e)
            })  
        }   
    })
</script>

