<script lang="ts">
    import Vue from 'vue'
    import VueToast from 'vue-toast-notification';
    // Import one of available themes
    import 'vue-toast-notification/dist/theme-default.css';
    //import 'vue-toast-notification/dist/theme-sugar.css';
    

    import AddCategoriesService from '../../shared/services/addCategory.service'
    import ProductService from '../../shared/services/product.service'

    import sideNav from '../../components/layout/side-nav.vue'
    import smSideNav from '../../components/layout/sm-side-nav.vue';
    // import HeaderOption from '@/pages/admin/Header-options'
    import HeaderOption from '@/components/common/Header-options'

    Vue.use(VueToast)
    const addCategoriesService = new AddCategoriesService();
    const productService = new ProductService();

    export default Vue.extend ({ 
        
    components : {
        'app-side-nav':sideNav,
        'app-sm-side-nav':smSideNav,
        'header-options':HeaderOption
    },
         data() {
            return {
                toast:Vue.$toast,
                productList:{},
                categoryList:{},
                AddSubcategory:{},
                sc_id:0,
                mc_id:0,
                headerOptions: {
                    createProduct:'true',
                    createCategory:'true',
                    createSubcategory:'false',
                    createBrand:'true'
                }
                // main_category_name:String
            }
        },
        methods: {
            getCategory() {
                productService.getAdminCategoryList()
                .then(response => {
                    this.categoryList = response.data.output.success[0].data
                    this.categoryList.forEach((element, index) => {
                        var tempObj = JSON.parse(this.categoryList[index].sub_category);
                        this.subcategoriesList[element['sub_category']]=[];
                            for(var i in tempObj){
                                var obj = {};
                                obj['subcategory_id'] = i;
                                obj['subcategory_name'] = tempObj[i];
                                this.subcategoriesList[element['sub_category']].push(obj);
                            }
                        this.categoryList[index]['sub_category']=this.subcategoriesList[element['sub_category']]
                        console.log("categorylist",this.categoryList)
                    }); 
                })
                .catch(e => {
                this.errors.push(e)
                })
            },
            createSubCategory(){
                let _subcategoryForm = new FormData()
                    Object.keys(this.AddSubcategory).forEach((key) => {
                        _subcategoryForm.append(key, this.AddSubcategory[key])
                    })
                addCategoriesService.createSubCategory(_subcategoryForm)
                .then(response => {
                   console.log(response);
                   this.AddSubcategory = {};
                   this.toast.success("New sub category is added",{
                        position:'bottom-right'
                   })
                })
                .catch(e => {
                    this.toast.error("Error occured while adding",{
                        position:'bottom-right'
                   })
                this.errors.push(e)
                })

            },
        },
         created() {
             this.getCategory();
            // productService.getProductList(this.mc_id,this.sc_id)
            // .then(response => {
            // this.productList = response.data.output.success[0].data;
            // console.log("this.productDEtails product details",this.productList)
            // })
            // .catch(e => {
            // this.errors.push(e)
            // })  
        }   
    })
</script>

