<script lang="ts">
    import Vue from 'vue'
    import VueToast from 'vue-toast-notification';
    // Import one of available themes
    import 'vue-toast-notification/dist/theme-default.css';
    //import 'vue-toast-notification/dist/theme-sugar.css';
    
    import AddCategoriesService from '../../shared/services/addCategory.service'
    // import HeaderOption from '@/pages/admin/Header-options'
    import HeaderOption from '@/components/common/Header-options'

    import sideNav from '../../components/layout/side-nav.vue'
    import smSideNav from '../../components/layout/sm-side-nav.vue';
    
    Vue.use(VueToast)
    const addCategoriesService = new AddCategoriesService();

    export default Vue.extend ({ 
        
    components : {
        'app-side-nav':sideNav,
        'app-sm-side-nav':smSideNav,
        'header-options':HeaderOption
    },
         data() {
            return {
                toast:Vue.$toast,
                productList:{},
                addCategories:{},
                sc_id:0,
                mc_id:0,
                headerOptions: {
                    createProduct:'true',
                    createCategory:'false',
                    createSubcategory:'true',
                    createBrand:'true'
                }
            }
        },
        methods: {           
            createCategory(){
                let _categoryForm = new FormData()
                    Object.keys(this.addCategories).forEach((key) => {
                        _categoryForm.append(key, this.addCategories[key])
                    })
                addCategoriesService.createCategory(_categoryForm)
                .then(response => {
                   console.log("response is reponse is",response);
                   this.toast.success("New category is added",{
                        position:'bottom-right'
                    })
                    this.addCategories={}
                    // window.location.href="/admin/productlist"
                })
                .catch(e => {
                    this.toast.error("Error occured while adding",{
                        position:'bottom-right'
                    })
                    this.errors.push(e)
                })

            },
        },
         created() {
         }
    })
</script>

