<template>
    <div class="home container p-3 mx-auto">
          <div class="row">
                <div class="col-md-3 d-none d-md-block">
                       <h3 style="width:">K-Work Tech</h3>
                </div>
                <carousel class="col-md-9" :autoplay="true" :nav="false" :responsive="{0:{items:1,nav:false},600:{items:3,nav:false}}">
                    <div class="p-2">
                          <app-product-card    v-bind:in_data="productList.new[0]" v-on:addProductList = "getProductWatchList"></app-product-card>
                    </div> 
                    <div class="p-2" >
                          <app-product-card    v-bind:in_data="productList.new[1]" v-on:addProductList = "getProductWatchList"></app-product-card>
                    </div> 
                    <div class="p-2" >
                          <app-product-card    v-bind:in_data="productList.new[2]" v-on:addProductList = "getProductWatchList"></app-product-card>
                    </div> 
                    <div class="p-2" >
                          <app-product-card    v-bind:in_data="productList.new[3]" v-on:addProductList = "getProductWatchList"></app-product-card>
                    </div> 
                    <div class="p-2" >
                          <app-product-card    v-bind:in_data="productList.new[4]" v-on:addProductList = "getProductWatchList"></app-product-card>
                    </div> 
                    <div class="p-2" >
                          <app-product-card   v-bind:in_data="productList.new[5]" v-on:addProductList = "getProductWatchList"></app-product-card>
                    </div> 

                </carousel>  
          </div>
          <div class="row border-top border-bottom border-dark font-weight-bold" style="display:flow-root">
                 <div class="offset-md-3 p-2 watchlist-block" data-toggle="modal" data-target="#isProductWatchList" v-if="productWatchList.length != 0"> 
                    <div class="watchlist">
                      <span>{{select_collection}} </span> <span class="watchlistcount pl-2">{{this.productWatchList.length}}</span>
                    </div> 
                  </div>
                  <div class="offset-md-3 p-2 watchlist-block" v-if="productWatchList.length == 0">
                    <div class="watchlist">
                      <span>{{select_collection}} </span> 
                    </div>
                  </div> 
          </div>
          <div class="modal fade" id="isProductWatchList" role="dialog">
            <div class="modal-dialog modal-md">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal">&times;</button>
                        <h4 class="modal-title">Product WatchList </h4>
                    </div>
                    <div class="modal-body">
                          <div class="template-body tab-bg pt-2">
                        <div class="table-responsive pt-2">
                            <table  class="table border pt-2">
                            <tbody  >
                                <tr>
                                <th>S No.</th>
                                <th>Brand Name</th>
                                <th>Product Name</th>
                                </tr>
                            </tbody>
                            {{isProductWatchList }}
                            <tbody>
                                <tr  v-for="(product,index) in productWatchList" :key="index">
                                <td >{{index+1}} </td>
                                <td>{{product.brand_desc}}</td>
                                <td>{{product.product_name}}</td>
                                </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                    </div>
                    <div class="modal-footer">
                        <!-- <button type="button" class="btn btn-default"  data-dismiss="modal">No</button>   
                        <button type="button" class="btn btn-primary" @click="addProduct()" data-dismiss="modal">YES</button> -->
                    </div>
                </div>
            </div>
        </div>  
          <div class="row">
            <div class="col-md-3">
                <app-side-nav class="d-none d-md-block pt-3" v-on:callProductList = "getProductList"></app-side-nav>
                <app-sm-side-nav class="d-block d-md-none pt-3" v-on:callProductList = "getProductList"></app-sm-side-nav>
            </div>
            <div class="col-md-9">
              <div class="row"> 
              </div>
              <div class="row">
                <div class="col-md-4 p-2"  v-for="product in productList.list" :key="product.brand_id">
                  <app-product-card  v-bind:in_data="product" v-on:addProductList = "getProductWatchList"></app-product-card>
                </div>
              </div>            
            </div>
          </div>
    </div>
</template>

<script>
// components
import ProductCard from '@/components/common/product-card'
// import ProductCardList from '@/components/common/product-card-list'
import sideNav from '@/components/layout/side-nav'
import smSideNav from '@/components/layout/sm-side-nav'
import ProductService from '../../shared/services/product.service'
import carousel from 'vue-owl-carousel'
   
const productService = new ProductService();


export default {
  name: 'Home',
  components: {
    'app-product-card': ProductCard,
    'app-side-nav': sideNav,
    'app-sm-side-nav': smSideNav,
    'carousel': carousel
  },
  props: {
    msg: String,
    listData:Object,
  },
   data() {
      return {
        productWatchList:[],
        productList:{},
        sc_id:0,
        mc_id:0,
        select_collection:"My Collection"
      }
    },
  methods: {
      getProductList(mc_id,sc_id,sel_name) {
        productService.getProductList(mc_id,sc_id)
        .then(response => {
          this.productList = response.data.output.success[0].data;
        })
        .catch(e => {
          this.errors.push(e)
        }) 
        
        this.setCollection(sel_name);
      },
      getProductWatchList(product){
        console.log("product product",product)
        this.productWatchList.push(product)
        console.log('addProductWatchList',this.productWatchList,this.productWatchList.length)
      },
      setCollection(sel_name){
        this.select_collection = sel_name;
      }
  },
  
  created() {
    productService.getProductList(this.mc_id,this.sc_id)
    .then(response => {
    this.productList = response.data.output.success[0].data;
      if(this.productList.new){
        this.productList.new.forEach(element => {
            element['product_status']='new' 
        });
      }
    console.log("this.productDEtails product details",this.productList)
    })
    .catch(e => {
    this.errors.push(e)
    })  
  }
}

</script>
<style  scoped>
  template{
    padding:2%;
  }
  .watchlist{
    text-transform:uppercase;
  }
  .watchlistcount{
    color: #007bff;
  }
  .watchlist-block{
    float: right;
    margin-right: 5px;
  }
</style>