
import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter);

// component
import Login from "@/components/authentication/login"
import Home from "@/pages/site/Home";
import Product from "@/pages/site/Product";

import AddProduct from "@/pages/admin/Product";
import ProductList from "@/pages/admin/Product-list"
import EditProduct from "@/pages/admin/Edit-Product"
import AddCategory from "@/pages/admin/Add-Category"
import AddSubCategory from "@/pages/admin/Add-SubCategory"
import AddBrands from "@/pages/admin/Add-Brands"
// import Admin from "@/pages/admin/index"
import _404 from "@/pages/404";


export default new VueRouter({
    routes:[
     
      {
          path: '/',
          name: 'Home', 
          component: Home
        },
        {
          path: '/product/:product_id',
          name: 'Product', 
          component:Product
        },
        {
          path:'/admin/login',
          name: 'Login',
          component:Login,
        },    
        {
          path: '/admin/productlist',
          name:'ProductList',
          component:ProductList
        },
        {
          path: '/admin/product/:product_id',
          name: 'AddProduct', 
          component:AddProduct
        },
        {
          path: '/admin/product/edit/:product_id',
          name: 'Edit Product',
          component: EditProduct
        },
        {
          path:'/admin/add-category',
          name: 'AddCategory',
          component:AddCategory
        },
        {
          path:'/admin/add-subcategory',
          name: 'AddSubCategory',
          component: AddSubCategory
        },
        {
          path:'/admin/add-brands',
          name: 'AddBrand',
          component:AddBrands
        },
        { 
          path: '*', 
          name: '_404', 
          component:_404
        }    
    ],
    mode: 'history'
  });