<template>

    <div class="sm-side-nav">
        <div class="row">
            <span style="font-size:30px;cursor:pointer" @click="openNav()">&#9776;</span>
        </div>
        <div id="mySidenav" class="row sidenav">
                <a href="javascript:void(0)" class="closebtn" @click="closeNav()">&times;</a>
                <div class="title p-3">K-Work Tech</div>
                <div class="sub-heading  pl-4">
                     <!-- <div @click="logout()">Logout</div> -->
                     <div @click="getLogin()" v-if="isLoginUser == null">Login</div>
                     <div @click="logout()" v-if="isLoginUser=='admin'">Logout</div>
                        <router-link to="/admin/productlist" v-if="isLoginUser=='admin'">admin product list</router-link>
                        <br/>
                        <router-link to="/admin/product/1" v-if="isLoginUser=='admin'">Create product</router-link>
                        
                    <div class="font-weight-bold" v-on:click="$emit('callProductList', 0,0)">ALL</div>
                    <ul class="list-group" v-for="item in categoryList" :key="item.main_category_id">
                        <li class="list-group-item text-uppercase  p-0" > 
                            <span class="font-weight-bold" v-on:click="$emit('callProductList', item.main_category_id, 0)">
                                {{item.main_category_name}}
                            </span>  
                            <ul class="list-group" v-for="subitem in item.sub_category" :key="subitem.subcategory_id"> 
                                <li class="list-group-item text-uppercase py-1" v-on:click="$emit('callProductList', item.main_category_id, subitem.subcategory_id)">{{subitem.subcategory_name}}</li>
                            </ul>
                        </li>
                    </ul>
                </div>
        </div>
    </div>

</template>
<script>

import Vue from 'vue'
import ProductService from '../../shared/services/product.service'
   
    const productService = new ProductService();

    export default Vue.extend ({  
        data() {
            return {
                categoryList :{},
                subcategoriesList:{},
                isLoginUser:String,
            }
          
        },
        created() {
            this.isLogin();
            productService.getCategoriesList()
            .then(response => {
            this.categoryList = response.data.output.success[0].data
             this.categoryList.forEach((element, index) => {
                    var tempObj = JSON.parse(this.categoryList[index].sub_category);
                    this.subcategoriesList[element['sub_category']]=[];
                        for(var i in tempObj){
                            var obj = {};
                            obj['subcategory_id'] = i;
                            obj['subcategory_name'] = tempObj[i];
                            this.subcategoriesList[element['sub_category']].push(obj);
                        }
                    this.categoryList[index]['sub_category']=this.subcategoriesList[element['sub_category']]
                }); 
            })
            .catch(e => {
            this.errors.push(e)
            })
        },
        methods: {
            openNav() {
                document.getElementById("mySidenav").style.width = "250px";
            },
            closeNav() {
                document.getElementById("mySidenav").style.width = "0";
            },
            getLogin(){
                // window.location.href="/admin/login"
                this.$router.push({path:'/admin/login'})
            },
            isLogin(){
                this.isLoginUser =  localStorage.getItem('isLoginUser');
                console.log('login user fjdkfjdafjlj',this.isLoginUser)
            },
            logout(){
                this.clear_localStorage();
                // window.location.href = "/admin/login"
                // window.location.href="/"
                this.$router.push({path:'/'})
            },
            clear_localStorage(){
                localStorage.removeItem('token');
                localStorage.removeItem('isLoginUser');
                localStorage.removeItem('username');
                console.log("localstoreg logout ",localStorage.getItem('isLoginUser'))
            }
        }

   
})

  
</script>

<style scoped>
.list-group-item{
    border: 0px solid grey!important;
} 


.sidenav {
  height: 100%;
  width: 0;
  position: fixed;
  background:#fff;
  border-left: 2px solid black;
  z-index: 1;
  top: 0;
  left: 0;
  overflow-x: hidden;
  transition: 0.5s;
}

/* .sidenav a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 25px;
  color: #818181;
  display: block;
  transition: 0.3s;
} */

.sidenav a:hover {
  color: #f1f1f1;
}

.sidenav .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
}
.font-weight-bold{
    font-weight: 600!important;
    color:#858585;
}

</style>